footer {
  background-color: #365182;
  color: #fcfcfc;
  padding: 60px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
footer > a {
  text-decoration: none;
  background-color: #b89602;
  color: #365182;
  border-radius: 5px;
  width: 100px;
  height: 20px;
  display: block;
  margin-top: 10px;
}
